<template>
  <div id="app">
    <header class='header'>
    </header>
    <main class='main'>
      <div class='photos'>
        <div class='photo' :class='{ active: item.name === active }' v-for='item in photos' :key='item.name'>
          <img class='img' :src='item.src' />
        </div>
      </div>
    </main>
    <footer>
      <img src='./assets/day.png' class='day' />
    </footer>
  </div>
</template>

<script>
/**
20200416 计划：
完成左右移动的动画效果
图片压缩

 */
export default {
  name: 'App',
  components: {
  },
  data () {
    return {
      photos: [],
      active: 1
    }
  },
  created () {
    for(let i=1;i<=10;i++){
      this.photos.push({ name: i, src: require(`./assets/${i}.jpg`)})
    }
  },
  mounted () {
    document.querySelectorAll('.photo').forEach(ele => {
      ele.addEventListener('touchstart', () => {
        let start = 0
        let end = 0
        let func = evt => {
          if (start === 0) start = evt.targetTouches[0].pageX
          end = evt.targetTouches[0].pageX
        }
        ele.addEventListener('touchmove', func)
        ele.addEventListener('touchend', () => {
          if (start - end > 50) this.active ++
          ele.removeEventListener('touchmove', func)
          console.log(start, end)
        })
      })
    })
  }
}
</script>

